import * as React from "react";
import styled from "styled-components";
import { Global } from "../../containers/App";
import { Link } from "react-router-dom";

interface ServiceSummaryProps {}

const Wrapper = styled.div`
    display: flex;
    flex-wrap: wrap;
    height: 170px;

    & > a {
        padding-left: 20px;
        padding-right: 20px;
        width: 33.3%;
        position: relative;
        font-size: 15px;
        color: black;
        text-decoration: none;

        &:first-child {
            padding-left: 0;
            border-right: 1px solid #eee;
        }

        &:last-child {
            padding-right: 0;
            border-left: 1px solid #eee;
        }
        & > p {
            font-size: 12px;
            font-weight: bold;
        }

        & > div {
            height: 70%;

            & > h3 {
                overflow: hidden;
                position: relative;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 1;
                margin: 6px 0 6px 0;
                padding: 4px 0 4px 0;
            }

            & > span {
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 2;
            }
        }

        & > a {
            display: block;
            width: 100%;
            position: absolute;
            bottom: 0;
            color: black;
            font-weight: bold;
            text-decoration: none;
        }

        & > div > h3:before {
            content: "";
            position: absolute;
            left: -1px;
            bottom: -1px;
            width: 100%;
            height: 2px;
            background-color: #231916;
            transform-origin: 0 0;
            transform: scaleX(0);
            transition: transform 0.3s cubic-bezier(0.215, 0.61, 0.355, 1), -webkit-transform 0.3s cubic-bezier(0.215, 0.61, 0.355, 1);
            z-index: 1000;
        }

        &:hover > div > h3:before {
            transform: scaleX(1);
        }
    }
`;

const ServiceSummary: React.SFC<ServiceSummaryProps> = props => {
    return (
        <>
            <Global />
            <Wrapper>
                <Link to={"#"}>
                    <p lang="en">PRESS RELEASE</p>
                    <div>
                        <h3>"일·거주·레저를 한번에" 소셜공간이 골목 살린다</h3>
                        <span>
                            유니온플레이스, 오는 9월 서울 영등포구 당산역에 소셜공간 2호점 론칭 계획 코람코자산운용이 펀드를 조성,
                            매입·리모델링하고 유니온플레이스가 10년간 운영을 맡아
                        </span>
                    </div>
                    <Link to={"/promotion"}>보도자료로</Link>
                </Link>
                <Link to={"#"}>
                    <p lang="en">CAREER</p>
                    <div>
                        <h3>따로 또 같이,</h3>
                        <span>자유로운 환경 속에서 우리의 일을 생각합니다.</span>
                    </div>
                    <Link to={"/career"}>채용 정보</Link>
                </Link>
                <Link to={"#"}>
                    <p lang="en">UNION CULTURE</p>
                    <div>
                        <h3>Culture, Career, Compensation.</h3>
                        <span> 우리는 3C를 통해 삶과 행복의 의미를 찾아갑니다.</span>
                    </div>
                    <Link to={"/union/culture"}>문화</Link>
                </Link>
            </Wrapper>
        </>
    );
};

export default ServiceSummary;



