import * as React from "react";
import styled from "styled-components";

import { Link } from "react-router-dom";

interface ServiceGridProps {}

const Wrapper = styled.div`
    display: flex;
    flex-wrap: wrap;
`;

const Element = styled.div`
    overflow: hidden;
    display: inline-block;
    position: relative;
    width: 33.3%;
    padding-top: 25%;
    background: no-repeat 50%;
    -webkit-background-size: cover;
    background-size: cover;
    cursor: pointer;

    & > a {
        display: block;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(58, 62, 80, 0.5);
        font-size: 15px;
        opacity: 0;
        filter: alpha(opacity=0);
        -webkit-transition: opacity 0.3s;
        transition: opacity 0.3s;
    }
    &:hover > a {
        opacity: 1;
    }
    &:hover > a > div {
        bottom: -5px;
    }

    &.first {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 42px 23px;
        /* border-top: 1px solid black; */
        border: 1px solid black;
        cursor: auto;

        & > div:nth-child(1) {
            & > div:nth-child(1) {
                width: 20%;
                height: 2px;
                background-color: black;
                margin-bottom: 5px;
            }
            & > div:nth-child(2) {
                font-size: 24px;
                font-weight: bold;
                font-style: normal;
                font-stretch: normal;
                line-height: 1.21;
                letter-spacing: normal;
            }
        }

        & > div:nth-child(2) {
            font-size: 14px;
            font-weight: bold;
            font-style: normal;
            font-stretch: normal;
            line-height: normal;
            letter-spacing: normal;
        }
    }
`;

const Dimmed = styled.div`
    position: absolute;
    left: 0px;
    bottom: -103px;
    width: 100%;
    color: #fff;
    transition: bottom 0.3s;

    & > img {
        width: 100%;
        height: 100%;
    }

    & > span {
        display: block;

        &:nth-child(1) {
            font-size: 22px;
            margin-bottom: 2px;
            font-weight: bold;
        }
    }
`;

const ServiceContent: React.SFC<ServiceGridProps> = props => {
    return (
        <Wrapper>
            <Element className="first">
                <div>
                    <div />
                    <div>Brands</div>
                </div>
                <div>
                    유니언 플레이스의 <br />
                    브랜드를 소개 합니다.{" "}
                </div>
            </Element>
            <Element style={{ backgroundImage: `url(${require("../../assets/main/brand1-basic.png")})` }}>
                <Link to={"/brand/sullivan"}>
                    <Dimmed>
                        <img src={require("../../assets/main/sullivan-cover.png")} alt="" />
                    </Dimmed>
                </Link>
            </Element>
            <Element style={{ backgroundImage: `url(${require("../../assets/main/brand2-basic.png")})` }}>
                <Link to={"/brand/upflo"}>
                    <Dimmed>
                        <img src={require("../../assets/main/upflo-cover.png")} alt="" />
                    </Dimmed>
                </Link>
            </Element>
            <Element style={{ backgroundImage: `url(${require("../../assets/main/brand3-basic.png")})` }}>
                <Link to={"/brand/nextkitchen"}>
                    <Dimmed>
                        <img src={require("../../assets/main/nextkitchen-cover.png")} alt="" />
                    </Dimmed>
                </Link>
            </Element>
            <Element style={{ backgroundImage: `url(${require("../../assets/main/brand4-basic.png")})` }}>
                <Link to={"/brand/joyland"}>
                    <Dimmed>
                        <img src={require("../../assets/main/joyland-cover.png")} alt="" />
                    </Dimmed>
                </Link>
            </Element>
            <Element style={{ backgroundImage: `url(${require("../../assets/main/brand5-basic.png")})` }}>
                <Link to={"/brand/upfit"}>
                    <Dimmed>
                        <img src={require("../../assets/main/upfit-cover.png")} alt="" />
                    </Dimmed>
                </Link>
            </Element>
        </Wrapper>
    );
};

export default ServiceContent;



