import * as React from "react";
import SlideBanner from "src/components/SlideBanner/SlideBanner";
import styled from "styled-components";
import ServiceSummary from "src/components/ServiceSummary/ServiceSummary";
import ServiceContent from "src/components/ServiceContent/ServiceContent";

export interface MainProps {}
const Content = styled.div`
    padding: 0 200px 200px 200px;

    & > div {
        margin-top: 40px;
    }
`;
export default class Main extends React.Component<MainProps, any> {
    public render() {
        return (
            <>
                <SlideBanner />
                <Content>
                    <ServiceSummary />
                    <ServiceContent />
                </Content>
            </>
        );
    }
}



