import * as React from "react";
import styled, { keyframes } from "styled-components";
import { Global } from "../../containers/App";
import { Link } from "react-router-dom";

import img1 from "../../assets/main/img-home-3.png";
import img2 from "../../assets/main/img-home-1.png";
import img3 from "../../assets/main/img-home-2.png";
import btnNext from "../../assets/right-chevron.png";
import btnPrev from "../../assets/left-chevron.png";

interface SlideBannerProps {}

const FadeIn = keyframes`
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}
`;
const Wrapper = styled.div`
    overflow: hidden;
    position: relative;
    height: 100%;
`;

const InfoContainer = styled.div`
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 1000;
`;

const Info = styled(Link)`
    display: ${(props: { show: boolean }) => (props.show ? "block" : "none")};
    animation: ${(props: { show: boolean }) => (props.show ? FadeIn : "")};
    animation-duration: 0.5s;

    position: relative;
    width: 100%;
    height: 100%;
    color: #fff;
`;

const Progress = styled.div`
    position: absolute;
    left: 160px;
    bottom: 180px;
    width: calc(100% - 320px);
    height: 2px;
    background-color: rgba(255, 255, 255, 0.2);

    & > span {
        position: absolute;
        left: 0;
        top: 0;
        height: 2px;
        background-color: #fff;
        -webkit-transition: width 0.3s;
        transition: width 0.3s;
    }
`;

const TextBox = styled.div`
    position: absolute;
    left: 160px;
    bottom: 140px;
    color: white;

    & > p:nth-child(1) {
        margin-bottom: 23px;
        font-size: 36px;
        line-height: 46px;

        & > strong {
            display: table-cell;
            vertical-align: middle;
            height: 92px;
            font-size: 23px;
            line-height: 38px;
            letter-spacing: -1px;

            text-shadow: 0 2px 4px rgba(0, 0, 0, 0.7);
        }
    }

    & > p:nth-child(2) {
        font-size: 15px;
        line-height: 1.6;

        & > span {
            display: table-cell;
            vertical-align: middle;
            height: 48px;
        }
    }
`;

const BtnNext = styled.button`
    position: absolute;
    bottom: 161px;
    width: 46px;
    right: 85px;
    height: 46px;
    background: url(${btnNext}) no-repeat;
    background-size: 100% 100%;
    opacity: 0.8;
    filter: alpha(opacity=80);
    -webkit-transition: opacity 0.3s;
    transition: opacity 0.3s;
    border: none;
    outline: none;

    &:hover {
        opacity: 1;
    }
`;

const BtnPrev = styled.button`
    position: absolute;
    bottom: 161px;
    left: 85px;
    width: 46px;
    height: 46px;
    background: url(${btnPrev}) no-repeat;
    background-size: 100% 100%;
    opacity: 0.8;
    filter: alpha(opacity=80);
    -webkit-transition: opacity 0.3s;
    transition: opacity 0.3s;
    border: none;
    outline: none;

    &:hover {
        opacity: 1;
    }
`;

const SwipeContainer = styled.div`
    display: flex;
    height: 100%;

    /* & > div {
        background-image: linear-gradient(to bottom, rgba(99, 99, 99, 0), rgba(96, 96, 96, 0.5) 41%, #616161);
    } */
`;

const PageIndicator = styled.div`
    position: absolute;
    left: 160px;
    bottom: 114px;
    font-size: 12px;
    color: #fff;
    display: flex;
    align-items: center;
`;

const calHeight = (width: number) => {
    return (766 * width) / 1920;
};

const SlideBanner: React.SFC<SlideBannerProps> = props => {
    const [isChange, setChange] = React.useState(false);
    const [viewIndex, setViewIndex] = React.useState(0);
    const [width, setWidth] = React.useState(0);
    const [height, setHeight] = React.useState(0);

    React.useEffect(() => {
        setWidth(innerWidth);
        setHeight(calHeight(innerWidth));
        window.addEventListener("resize", () => {
            setWidth(innerWidth);
            setHeight(calHeight(innerWidth));
        });
        return () =>
            window.removeEventListener("resize", () => {
                setWidth(innerWidth);
                setHeight(calHeight(innerWidth));
            });
    }, [width, height]);
    return (
        <>
            <Global />
            <Wrapper style={{ height: `${height}px` }}>
                <InfoContainer>
                    <Info to="#" show={viewIndex % 3 === 0}>
                        <TextBox>
                            <p>
                                <strong>사람을 향한 열린 공간브랜드를 만듭니다.</strong>
                            </p>
                            <p>
                                <span />
                            </p>
                        </TextBox>
                    </Info>
                    <Info to="#" show={viewIndex % 3 === 1}>
                        <TextBox>
                            <p>
                                <strong>
                                    라이프 스타일의 재탄생. <br />
                                    우리는 도시재생의 패러다임을 만들어 갑니다.
                                </strong>
                            </p>
                            <p>
                                <span />
                            </p>
                        </TextBox>
                    </Info>
                    <Info to="#" show={viewIndex % 3 === 2}>
                        <TextBox>
                            <p>
                                <strong>
                                    사람이 만나 새로운 관계를 맺고, 상상하지 못한 시너지를 만들어내는 과정. <br />
                                    우리는 그 어울림에 공감했고 그것이 가진 힘에 주목해습니다.
                                </strong>
                            </p>
                            <p>
                                <span />
                            </p>
                        </TextBox>
                    </Info>
                    <Progress>
                        <span style={{ width: `${33 * ((viewIndex % 3) + 1)}%` }} />
                    </Progress>
                    <BtnNext
                        onClick={async () => {
                            if (!isChange) {
                                if (viewIndex % 5 === 3) {
                                    await setViewIndex(0);
                                    await setTimeout(() => {
                                        setViewIndex(1);
                                        setChange(true);
                                        setTimeout(() => {
                                            setChange(false);
                                        }, 1000);
                                    }, 10);
                                } else {
                                    setViewIndex((viewIndex + 1) % 5);
                                    setChange(true);
                                    setTimeout(() => {
                                        setChange(false);
                                    }, 1000);
                                }
                            }
                        }}
                    />
                    <BtnPrev
                        onClick={async () => {
                            if (!isChange) {
                                if (viewIndex % 5 === 0) {
                                    console.log(viewIndex);
                                    await setViewIndex(3);
                                    await setTimeout(() => {
                                        setViewIndex(2);
                                        setChange(true);
                                        setTimeout(() => {
                                            setChange(false);
                                        }, 1000);
                                    }, 10);
                                } else {
                                    setViewIndex((viewIndex - 1) % 5);
                                    setChange(true);
                                    setTimeout(() => {
                                        setChange(false);
                                    }, 1000);
                                }
                            }
                        }}
                    />
                    <PageIndicator>
                        <strong>{(viewIndex % 3) + 1}</strong>
                        <span
                            style={{ display: "block", width: "1px", height: "8px", backgroundColor: "#d6d6d5", margin: "0 5px 0 5px" }}
                        />
                        <span>3</span>
                    </PageIndicator>
                </InfoContainer>
                <SwipeContainer
                    style={{
                        width: `${width * 5}px`,
                        transform: `translate3d(-${width * ((viewIndex + 1) % 5)}px, 0px, 0px)`,
                        transitionDuration: isChange ? "1000ms" : "0ms"
                    }}
                >
                    <div style={{ width: `${width}px` }} data-index="2">
                        <img src={img3} alt="" style={{ width: "100%" }} />
                    </div>
                    <div style={{ width: `${width}px` }} data-index="0">
                        <img src={img1} alt="" style={{ width: "100%" }} />
                    </div>

                    <div style={{ width: `${width}px` }} data-index="1">
                        <img src={img2} alt="" style={{ width: "100%" }} />
                    </div>

                    <div style={{ width: `${width}px` }} data-index="2">
                        <img src={img3} alt="" style={{ width: "100%" }} />
                    </div>
                    <div style={{ width: `${width}px` }} data-index="0">
                        <img src={img1} alt="" style={{ width: "100%" }} />
                    </div>
                </SwipeContainer>
            </Wrapper>
        </>
    );
};

export default SlideBanner;



